<template>
    <div class="worldmap" v-on:click="click">
        <vue-world-map id="worldmap" v-bind:countryData="countries" v-bind:defaultCountryFillColor="map.defaultCountryFillColor" v-bind:highColor='highColor' v-bind:countryStrokeColor='map.countryStrokeColor' v-bind:lowColor="lowColor"/>
    </div>
</template>

<script>

import VueWorldMap from 'vue-world-map'

export default {
  components: {
    VueWorldMap
  },
  props: {
    countries: Object,
    highColor: String,
    lowColor: String
  },
  methods: {
    click: function (event) {
      if (event.target.id !== 'map-svg') {
        this.$emit('click', event.target.id)
      }
    }
  },
  data: function () {
    return {
      map: {
        defaultCountryFillColor: '#777',
        highColor: 'rgb(160, 247, 119)',
        lowColor: 'rgb(239, 77, 88)',
        countryStrokeColor: '#363636'
      }
    }
  }
}
</script>
