  <div id="app">
    <b-navbar type="is-dark" wrapper-class="container">
      <template slot="brand">
          <b-navbar-item tag="router-link" :to="{ path: '/' }">
              <img
                  src="./assets/2iqlogo.png"
                  alt="2IQ"
              >
              <h1>Global Insider Dealing Monitor</h1>
          </b-navbar-item>
      </template>
    </b-navbar>
    <div class="container">
      <b-tabs @change="onTabChange" size="is-medium" expanded>
        <b-tab-item label="Buy/Sell Ratio"/>
        <b-tab-item label="Buying Volume"/>
        <b-tab-item label="Selling Volume"/>
        <b-tab-item label="% Companies with Net Buying"/>
      </b-tabs>
      <div class="block has-text-right top-switch">
        <b-switch v-model="showBooks">Show Only Top Insiders</b-switch>
      </div>
      <WorldMap v-bind:countries="countries" v-bind:lowColor="lowColor" v-bind:highColor="highColor" @click="selectCountry"></WorldMap>
      <div class="map-slider">
        <p class="is-size-6">{{ monthTxt }}</p>
        <vue-slider v-model="yearMonth" :data="months" :marks="false"></vue-slider>
      </div>
      <!--<div class="has-text-left is-size-1 country">
        <CountryFlag v-bind:iso="countryCode" mode="squared" />
        {{ countryName }}
      </div>-->
      <div class="columns">
        <div class="column">
          <v-select :options="countryOptions" label="name" :value="{ code: countryCode, name: countryName }" @input="selectCountryDd" class="country-select">
            <template v-slot:option="option">
              <div class="has-text-left is-size-4 country-option">
                <CountryFlag v-bind:iso="option.code" mode="squared" />
                {{ option.name }}
              </div>
            </template>
            <template v-slot:selected-option="option">
              <div class="has-text-left is-size-1 country-option">
                <CountryFlag v-bind:iso="option.code" mode="squared" />
                {{ option.name }}
              </div>
            </template>
          </v-select>
        </div>
        <div class="column">
          <div style="height: 6rem"></div>
        </div>
      </div>
      <div class="columns is-desktop">
        <div class="column">
          <div class="card">
            <header class="card-header">
              <p class="card-header-title">
                Buy/Sell Ratio
              </p>
            </header>
            <div class="card-content">
              <div class="content">
                <apexchart type="line" :options="buySellRatioOptions" :series="buySellSeries"></apexchart>
              </div>
            </div>
          </div>
          <div class="card">
            <header class="card-header">
              <p class="card-header-title">
                Buy Volume
              </p>
            </header>
            <div class="card-content">
              <div class="content">
                <apexchart type="line" :options="buyVolumeOptions" :series="buyVolumeSeries"></apexchart>
              </div>
            </div>
          </div>
          <div class="card">
            <header class="card-header">
              <p class="card-header-title">
                Sell Volume
              </p>
            </header>
            <div class="card-content">
              <div class="content">
                <apexchart type="line" :options="buyVolumeOptions" :series="sellVolumeSeries"></apexchart>
              </div>
            </div>
          </div>
        </div>
        <div class="column">
          <div class="card">
            <header class="card-header">
              <p class="card-header-title">
                History
              </p>
            </header>
            <div class="card-content">
              <div class="content">
                <table class="table is-striped is-bordered">
                  <thead>
                    <tr>
                      <th class="has-text-centered">Month</th>
                      <th class="has-text-centered">Buy/Sell</th>
                      <th class="has-text-centered">Buy<br/>Volume</th>
                      <th class="has-text-centered">Sell<br/>Volume</th>
                      <th class="has-text-centered">% Net<br/>Buying</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="row in countryData" v-bind:key="row.yearMonth">
                      <td>{{ (String(row.yearMonth).substring(0, 4) + "-" + String(row.yearMonth).substring(4, 6)) }}</td>
                      <td v-bind:style="{ backgroundColor: getColor(row.buySellRatioPercentile) }" class="has-text-black-ter has-text-right"><b>{{ row.buySellRatio }}</b></td>
                      <td v-bind:style="{ backgroundColor: getColor(row.buyVolumePercentile) }" class="has-text-black-ter has-text-right"><b>{{ formatNumber(row.buyVolume) }}</b></td>
                      <td v-bind:style="{ backgroundColor: getColor(row.sellVolumePercentile) }" class="has-text-black-ter has-text-right"><b>{{ formatNumber(row.sellVolume) }}</b></td>
                      <td v-bind:style="{ backgroundColor: getColor(row.netBuyingCompaniesPercPercentile) }" class="has-text-black-ter has-text-right"><b>{{ row.netBuyingCompaniesPerc.toPrecision(3) }}</b></td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div>
      <br/>
    </div>
    <!--
    <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>
    <router-view/>-->
  </div>
</template>

<script>
import WorldMap from './components/WorldMap.vue'
import axios from 'axios'
import { TAB_MAIN } from './enums.js'
import VueSlider from 'vue-slider-component'
import CountryFlag from '@dzangolab/vue-country-flag-icon'
import 'vue-slider-component/theme/default.css'
import chartOptions from './chart-options.js'

var getColor = function (value) {
  var r = Math.round(239 + (160 - 239) * value)
  var g = Math.round(77 + (247 - 77) * value)
  var b = Math.round(88 + (119 - 88) * value)
  return 'rgb(' + r + ', ' + g + ', ' + b + ')'
}

var formatNumber = function (value) {
  if (value) {
    if (value >= 1000000000) {
      return (value / 1000000000).toPrecision(3) + 'b'
    } else if (value >= 1000000) {
      return (value / 1000000).toPrecision(3) + 'm'
    }
    return value.toPrecision(6)
  } else return '-'
}

var buySellRatioOptions = Object.assign({}, chartOptions)
var buyVolumeOptions = Object.assign({}, chartOptions)
buyVolumeOptions.yaxis.labels.formatter = function (value, timestamp, index) {
  return formatNumber(value)
}

export default {
  components: {
    WorldMap,
    VueSlider,
    CountryFlag
  },
  data: function () {
    return {
      buySellRatioOptions: buySellRatioOptions,
      buyVolumeOptions: buyVolumeOptions
    }
  },
  methods: {
    onTabChange (value) {
      switch (value) {
        case 0:
          this.$store.commit('selectTab', TAB_MAIN.BUYSELLRATIO)
          break
        case 1:
          this.$store.commit('selectTab', TAB_MAIN.BUYVOLUME)
          break
        case 2:
          this.$store.commit('selectTab', TAB_MAIN.SELLVOLUME)
          break
        case 3:
          this.$store.commit('selectTab', TAB_MAIN.NETBUYING)
          break
      }
    },
    selectCountry (country) {
      this.$store.state.selectedCountry = country
    },
    selectCountryDd (option) {
      this.selectCountry(option.code)
    },
    getColor (value) {
      return getColor(value)
    },
    formatNumber (value) {
      return formatNumber(value)
    }
  },
  computed: {
    data: function () { return this.$store.state.data },
    months: function () {
      return Object.keys(this.$store.state.data)
    },
    countryName: function () {
      if (this.$store.state.selectedCountry) {
        return this.$store.state.countryNameMap[this.$store.state.selectedCountry]
      } else return ''
    },
    countryCode: function () {
      if (this.$store.state.selectedCountry) {
        return this.$store.state.selectedCountry
      } else return ''
    },
    countryOptions: function () {
      var countryMap = this.$store.state.countryNameMap
      var options = Object.keys(this.$store.state.dataByCountry).map(function (c) {
        return {
          name: countryMap[c],
          code: c
        }
      }).filter(a => a.name)
      options.sort(function (a, b) {
        return a.name.toLowerCase().localeCompare(b.name.toLowerCase())
      })
      return options
    },
    monthTxt: function () {
      var year = this.$store.state.yearMonth.substring(0, 4)
      switch (this.$store.state.yearMonth.substring(4, 6)) {
        case '01':
          return 'January ' + year
        case '02':
          return 'February ' + year
        case '03':
          return 'March ' + year
        case '04':
          return 'April ' + year
        case '05':
          return 'May ' + year
        case '06':
          return 'June ' + year
        case '07':
          return 'July ' + year
        case '08':
          return 'August ' + year
        case '09':
          return 'September ' + year
        case '10':
          return 'October ' + year
        case '11':
          return 'November ' + year
        case '12':
          return 'December ' + year
      }
      return ''
    },
    yearMonth: {
      get () {
        return this.$store.state.yearMonth
      },
      set (value) {
        this.$store.state.yearMonth = value
      }
    },
    countries: function () {
      if (this.$store.state.data[this.$store.state.yearMonth]) {
        return this.$store.state.data[this.$store.state.yearMonth][this.$store.state.activeTab].countries
      } else {
        return {}
      }
    },
    countryData: function () {
      if (this.$store.state.selectedCountry) {
        return this.$store.state.dataByCountry[this.$store.state.selectedCountry]
      } else return ''
    },
    buySellSeries: function () {
      if (this.$store.state.selectedCountry && this.$store.state.dataByCountry[this.$store.state.selectedCountry]) {
        var data = this.$store.state.dataByCountry[this.$store.state.selectedCountry].slice()
        return [{
          name: 'Buy/Sell Ratio',
          data: data.sort((a, b) => a.yearMonth - b.yearMonth).map(function (row) {
            return {
              x: String(row.yearMonth).substring(0, 4) + '-' + String(row.yearMonth).substring(4, 6),
              y: row.buySellRatio
            }
          })
        }]
      } else return []
    },
    buyVolumeSeries: function () {
      if (this.$store.state.selectedCountry && this.$store.state.dataByCountry[this.$store.state.selectedCountry]) {
        var data = this.$store.state.dataByCountry[this.$store.state.selectedCountry].slice()
        return [{
          name: 'Buy Volume',
          data: data.sort((a, b) => a.yearMonth - b.yearMonth).map(function (row) {
            return {
              x: String(row.yearMonth).substring(0, 4) + '-' + String(row.yearMonth).substring(4, 6),
              y: row.buyVolume
            }
          })
        }]
      } else return []
    },
    sellVolumeSeries: function () {
      if (this.$store.state.selectedCountry && this.$store.state.dataByCountry[this.$store.state.selectedCountry]) {
        var data = this.$store.state.dataByCountry[this.$store.state.selectedCountry].slice()
        return [{
          name: 'Sell Volume',
          data: data.sort((a, b) => a.yearMonth - b.yearMonth).map(function (row) {
            return {
              x: String(row.yearMonth).substring(0, 4) + '-' + String(row.yearMonth).substring(4, 6),
              y: row.sellVolume
            }
          })
        }]
      } else return []
    },
    lowColor: function () {
      if (this.$store.state.data[this.$store.state.yearMonth]) {
        var min = this.$store.state.data[this.$store.state.yearMonth][this.$store.state.activeTab].min
        return getColor(min)
      } else return 'rgb(239, 77, 88)'
    },
    highColor: function () {
      if (this.$store.state.data[this.$store.state.yearMonth]) {
        var max = this.$store.state.data[this.$store.state.yearMonth][this.$store.state.activeTab].max
        return getColor(max)
      } else return 'rgb(160, 247, 119)'
    }
  },
  created: function () {
    axios
      .get('/countries')
      .then(response => (this.$store.commit('setData', response.data)))
  }
}
</script>

