import Vue from 'vue'
import Vuex from 'vuex'
import { TAB_MAIN } from '../enums.js'
import countryCodes from '../country_codes.json'

Vue.use(Vuex)

var countryCodeMap = {}
var countryNameMap = {}
for (var i = 0; i < countryCodes.length; i++) {
  countryCodeMap[countryCodes[i].code3] = countryCodes[i].code2
  countryNameMap[countryCodes[i].code2] = countryCodes[i].name
}

export default new Vuex.Store({
  state: {
    countryCodes: countryCodes,
    countryCodeMap: countryCodeMap,
    countryNameMap: countryNameMap,
    selectedCountry: 'US',
    activeTab: TAB_MAIN.BUYSELLRATIO,
    data: {},
    countries: {},
    yearMonth: '202003',
    dataByCountry: {}
  },
  mutations: {
    selectTab (state, tab) {
      state.activeTab = tab
    },
    setData (state, data) {
      var yearMonths = {}
      var countries = {}
      for (var i = 0; i < data.length; i++) {
        yearMonths[data[i].yearMonth] = true
        countries[data[i].locIso3] = true
      }
      var processed = {}
      var processedByCountry = {}
      for (var country in countries) {
        processedByCountry[countryCodeMap[country]] = data.filter(a => a.locIso3 === country).sort((a, b) => b.yearMonth - a.yearMonth)
      }
      for (var yearMonth in yearMonths) {
        processed[yearMonth] = {}
        var processField = function (field) {
          var dataMonth = data.filter(a => String(a.yearMonth) === yearMonth)
          var countries = {}
          var min = 1
          var max = 0
          for (var i = 0; i < dataMonth.length; i++) {
            var code2 = countryCodeMap[dataMonth[i].locIso3]
            var value = dataMonth[i][field + 'Percentile']
            if (value) {
              countries[code2] = value
              if (value < min) {
                min = value
              }
              if (value > max) {
                max = value
              }
            }
          }
          processed[yearMonth][field] = {}
          processed[yearMonth][field].countries = countries
          processed[yearMonth][field].min = min
          processed[yearMonth][field].max = max
        }
        processField(TAB_MAIN.BUYSELLRATIO)
        processField(TAB_MAIN.BUYVOLUME)
        processField(TAB_MAIN.SELLVOLUME)
        processField(TAB_MAIN.NETBUYING)
      }
      state.data = processed
      state.dataByCountry = processedByCountry
    }
  },
  actions: {
  },
  modules: {
  }
})
